.edit-profile {
    overflow: hidden;
}

.edit-profile form{
    display: grid;
    grid-template-columns: 1fr 0.1fr 1fr;
    row-gap: 1em;
}

.edit-profile input[type="file"] {
    display: none;
}

.edit-profile h2{
    grid-column: 1 / 2;
}

.edit-profile :where(label) {
    grid-column: -1 / -2;
}

.edit-profile label{
    justify-self: end;
}

.edit-profile input[placeholder="Location"] {
    grid-column: -1 / -2;
}

.edit-profile input {
    grid-column: 1 / span 1;
    outline: none;
    border: none;
    background-color: var(--clr-background);
    font-size: 0.8rem;
    border-radius: 5px;
    width: 100%;
}

.edit-profile button {
    color: white;    
    border-radius: 5px;
    grid-column: 1 / -1;
    justify-self: center;
    background-color: var(--clr-btn-primary);
    transition: background-color .2s;
}

.edit-profile button:hover {
    background-color: var(--clr-btn-primary-dark);
}

.edit-profile label:has(i) + img{
    width: 20%;
    max-width: 100%;
    grid-column: 1 / -1;
    justify-self: center;
    aspect-ratio: 1;
    border-radius: 50%;
    object-fit: cover;
}

.edit-profile > form > label:has(i) + img:nth-of-type(2){
    width: 40%;
    aspect-ratio: 16 / 9;
    border-radius: 10px;
}


